import { useState, useRef } from "react";
import { useNavigate } from "react-router-dom";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import { useTranslation } from "react-i18next";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Label } from "@/components/ui/label";
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group";
import {
  Form,
  FormControl,
  FormDescription,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { useCreateActivityRequest } from "@/hooks/useActivityRequests";
import { useUserRole } from "@/hooks/useUserRole";
import { useAuth } from "@/contexts/AuthContext";
import Navigation from "@/components/layout/Navigation";
import { ArrowLeft, Calendar, AlertCircle, Upload, Link as LinkIcon, X, Loader2 } from "lucide-react";
import { supabase } from "@/integrations/supabase/client";
import { toast } from "@/hooks/use-toast";

const formSchema = z.object({
  title_zh: z.string().min(1, "中文标题不能为空"),
  title_en: z.string().min(1, "英文标题不能为空"),
  title_pt: z.string().min(1, "葡萄牙语标题不能为空"),
  intro_zh: z.string().optional(),
  intro_en: z.string().optional(),
  intro_pt: z.string().optional(),
  content_zh: z.string().min(1, "中文内容不能为空"),
  content_en: z.string().min(1, "英文内容不能为空"),
  content_pt: z.string().min(1, "葡萄牙语内容不能为空"),
  image_url: z.string().url().optional().or(z.literal("")),
  start_date: z.string().min(1, "开始日期不能为空"),
  end_date: z.string().optional(),
  service_type: z.enum(["dine_in", "takeaway", "both"]),
  badge_text_zh: z.string().optional(),
  badge_text_en: z.string().optional(),
  badge_text_pt: z.string().optional(),
  badge_color: z.string().optional(),
});

type FormData = z.infer<typeof formSchema>;

const MerchantActivityRequestForm = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const { user } = useAuth();
  const { isMerchant, isSupervisor } = useUserRole();
  const createMutation = useCreateActivityRequest();
  const [loading, setLoading] = useState(false);
  const [imageMode, setImageMode] = useState<"upload" | "url">("upload");
  const [uploadedImageUrl, setUploadedImageUrl] = useState<string>("");
  const [uploading, setUploading] = useState(false);
  const [previewImage, setPreviewImage] = useState<string>("");
  const fileInputRef = useRef<HTMLInputElement>(null);

  const handleImageUpload = async (file: File) => {
    if (!user) return;
    
    if (file.size > 5 * 1024 * 1024) {
      toast({
        title: "图片太大",
        description: "图片大小不能超过5MB",
        variant: "destructive",
      });
      return;
    }

    setUploading(true);
    try {
      const fileExt = file.name.split(".").pop();
      const fileName = `${user.id}-${Date.now()}.${fileExt}`;
      const filePath = `activity-requests/${fileName}`;

      const { error: uploadError } = await supabase.storage
        .from("activity-images")
        .upload(filePath, file);

      if (uploadError) throw uploadError;

      const { data: { publicUrl } } = supabase.storage
        .from("activity-images")
        .getPublicUrl(filePath);

      setUploadedImageUrl(publicUrl);
      setPreviewImage(publicUrl);
      toast({
        title: "上传成功",
        description: "封面图片已上传",
      });
    } catch (error) {
      console.error("Upload error:", error);
      toast({
        title: "上传失败",
        description: "图片上传失败，请重试",
        variant: "destructive",
      });
    } finally {
      setUploading(false);
    }
  };

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      handleImageUpload(file);
    }
  };

  const clearUploadedImage = () => {
    setUploadedImageUrl("");
    setPreviewImage("");
    if (fileInputRef.current) {
      fileInputRef.current.value = "";
    }
  };

  const form = useForm<FormData>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      title_zh: "",
      title_en: "",
      title_pt: "",
      intro_zh: "",
      intro_en: "",
      intro_pt: "",
      content_zh: "",
      content_en: "",
      content_pt: "",
      image_url: "",
      start_date: new Date().toISOString().split("T")[0],
      end_date: "",
      service_type: "both",
      badge_text_zh: "",
      badge_text_en: "",
      badge_text_pt: "",
      badge_color: "#9b87f5",
    },
  });

  // 检查权限
  if (!user || (!isMerchant && !isSupervisor)) {
    return (
      <div className="min-h-screen bg-background">
        <Navigation />
        <div className="container mx-auto px-4 py-8">
          <Card>
            <CardContent className="flex flex-col items-center justify-center py-12">
              <AlertCircle className="h-12 w-12 text-muted-foreground mb-4" />
              <p className="text-lg font-medium">无权访问</p>
              <p className="text-sm text-muted-foreground">
                只有商家和主管可以申请创建活动
              </p>
            </CardContent>
          </Card>
        </div>
      </div>
    );
  }

  const onSubmit = async (data: FormData) => {
    setLoading(true);
    try {
      // Determine the final image URL based on mode
      const finalImageUrl = imageMode === "upload" ? uploadedImageUrl : data.image_url;
      
      await createMutation.mutateAsync({
        title_zh: data.title_zh,
        title_en: data.title_en,
        title_pt: data.title_pt,
        intro_zh: data.intro_zh || null,
        intro_en: data.intro_en || null,
        intro_pt: data.intro_pt || null,
        content_zh: data.content_zh,
        content_en: data.content_en,
        content_pt: data.content_pt,
        image_url: finalImageUrl || null,
        start_date: data.start_date,
        end_date: data.end_date || null,
        service_type: data.service_type,
        badge_text_zh: data.badge_text_zh || null,
        badge_text_en: data.badge_text_en || null,
        badge_text_pt: data.badge_text_pt || null,
        badge_color: data.badge_color || "#9b87f5",
      });
      navigate("/merchant/activity-requests");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-background">
      <Navigation />
      <div className="container mx-auto px-4 py-8">
        <div className="max-w-3xl mx-auto space-y-6">
          <div className="flex items-center gap-4">
            <Button
              variant="ghost"
              size="icon"
              onClick={() => navigate("/merchant/activity-requests")}
            >
              <ArrowLeft className="h-4 w-4" />
            </Button>
            <div>
              <h1 className="text-2xl font-bold">申请创建活动</h1>
              <p className="text-muted-foreground">
                填写活动信息，提交后等待管理员审核
              </p>
            </div>
          </div>

          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Calendar className="h-5 w-5" />
                活动信息
              </CardTitle>
              <CardDescription>
                请填写完整的活动信息，审核通过后将自动创建活动
              </CardDescription>
            </CardHeader>
            <CardContent>
              <Form {...form}>
                <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
                  {/* 标题 */}
                  <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                    <FormField
                      control={form.control}
                      name="title_zh"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>中文标题 *</FormLabel>
                          <FormControl>
                            <Input placeholder="活动标题" {...field} />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                    <FormField
                      control={form.control}
                      name="title_en"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>英文标题 *</FormLabel>
                          <FormControl>
                            <Input placeholder="Activity Title" {...field} />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                    <FormField
                      control={form.control}
                      name="title_pt"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>葡萄牙语标题 *</FormLabel>
                          <FormControl>
                            <Input placeholder="Título da Atividade" {...field} />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                  </div>

                  {/* 简介 */}
                  <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                    <FormField
                      control={form.control}
                      name="intro_zh"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>中文简介</FormLabel>
                          <FormControl>
                            <Input placeholder="简短描述" {...field} />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                    <FormField
                      control={form.control}
                      name="intro_en"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>英文简介</FormLabel>
                          <FormControl>
                            <Input placeholder="Brief description" {...field} />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                    <FormField
                      control={form.control}
                      name="intro_pt"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>葡萄牙语简介</FormLabel>
                          <FormControl>
                            <Input placeholder="Descrição breve" {...field} />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                  </div>

                  {/* 内容 */}
                  <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                    <FormField
                      control={form.control}
                      name="content_zh"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>中文内容 *</FormLabel>
                          <FormControl>
                            <Textarea placeholder="详细描述" rows={5} {...field} />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                    <FormField
                      control={form.control}
                      name="content_en"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>英文内容 *</FormLabel>
                          <FormControl>
                            <Textarea placeholder="Detailed description" rows={5} {...field} />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                    <FormField
                      control={form.control}
                      name="content_pt"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>葡萄牙语内容 *</FormLabel>
                          <FormControl>
                            <Textarea placeholder="Descrição detalhada" rows={5} {...field} />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                  </div>

                  {/* 图片和服务类型 */}
                  <div className="space-y-4">
                    <div className="space-y-3">
                      <Label>封面图片</Label>
                      <RadioGroup
                        value={imageMode}
                        onValueChange={(value) => {
                          setImageMode(value as "upload" | "url");
                          if (value === "url") {
                            clearUploadedImage();
                          } else {
                            form.setValue("image_url", "");
                            setPreviewImage("");
                          }
                        }}
                        className="flex gap-4"
                      >
                        <div className="flex items-center space-x-2">
                          <RadioGroupItem value="upload" id="upload" />
                          <Label htmlFor="upload" className="flex items-center gap-1 cursor-pointer">
                            <Upload className="h-4 w-4" />
                            上传图片
                          </Label>
                        </div>
                        <div className="flex items-center space-x-2">
                          <RadioGroupItem value="url" id="url" />
                          <Label htmlFor="url" className="flex items-center gap-1 cursor-pointer">
                            <LinkIcon className="h-4 w-4" />
                            图片URL
                          </Label>
                        </div>
                      </RadioGroup>

                      {imageMode === "upload" ? (
                        <div className="space-y-3">
                          <div className="flex items-center gap-2">
                            <Input
                              ref={fileInputRef}
                              type="file"
                              accept="image/*"
                              onChange={handleFileChange}
                              disabled={uploading}
                              className="flex-1"
                            />
                            {uploading && <Loader2 className="h-4 w-4 animate-spin" />}
                          </div>
                          {previewImage && (
                            <div className="relative inline-block">
                              <img
                                src={previewImage}
                                alt="Preview"
                                className="max-h-32 rounded-md border"
                              />
                              <Button
                                type="button"
                                variant="destructive"
                                size="icon"
                                className="absolute -top-2 -right-2 h-6 w-6"
                                onClick={clearUploadedImage}
                              >
                                <X className="h-3 w-3" />
                              </Button>
                            </div>
                          )}
                          <p className="text-xs text-muted-foreground">
                            支持 JPG、PNG、WebP 格式，最大 5MB
                          </p>
                        </div>
                      ) : (
                        <FormField
                          control={form.control}
                          name="image_url"
                          render={({ field }) => (
                            <FormItem>
                              <FormControl>
                                <Input
                                  placeholder="https://example.com/image.jpg"
                                  {...field}
                                  onChange={(e) => {
                                    field.onChange(e);
                                    setPreviewImage(e.target.value);
                                  }}
                                />
                              </FormControl>
                              {previewImage && (
                                <div className="mt-2">
                                  <img
                                    src={previewImage}
                                    alt="Preview"
                                    className="max-h-32 rounded-md border"
                                    onError={() => setPreviewImage("")}
                                  />
                                </div>
                              )}
                              <FormMessage />
                            </FormItem>
                          )}
                        />
                      )}
                    </div>
                  </div>

                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div /> {/* Spacer for alignment */}
                    <FormField
                      control={form.control}
                      name="service_type"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>服务类型 *</FormLabel>
                          <Select onValueChange={field.onChange} defaultValue={field.value}>
                            <FormControl>
                              <SelectTrigger>
                                <SelectValue placeholder="选择服务类型" />
                              </SelectTrigger>
                            </FormControl>
                            <SelectContent>
                              <SelectItem value="dine_in">堂食</SelectItem>
                              <SelectItem value="takeaway">外卖</SelectItem>
                              <SelectItem value="both">堂食+外卖</SelectItem>
                            </SelectContent>
                          </Select>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                  </div>

                  {/* 日期 */}
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <FormField
                      control={form.control}
                      name="start_date"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>开始日期 *</FormLabel>
                          <FormControl>
                            <Input type="date" {...field} />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                    <FormField
                      control={form.control}
                      name="end_date"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>结束日期</FormLabel>
                          <FormControl>
                            <Input type="date" {...field} />
                          </FormControl>
                          <FormDescription>留空表示无限期</FormDescription>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                  </div>

                  {/* 徽章 */}
                  <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
                    <FormField
                      control={form.control}
                      name="badge_text_zh"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>中文徽章</FormLabel>
                          <FormControl>
                            <Input placeholder="新活动" {...field} />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                    <FormField
                      control={form.control}
                      name="badge_text_en"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>英文徽章</FormLabel>
                          <FormControl>
                            <Input placeholder="New" {...field} />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                    <FormField
                      control={form.control}
                      name="badge_text_pt"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>葡萄牙语徽章</FormLabel>
                          <FormControl>
                            <Input placeholder="Novo" {...field} />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                    <FormField
                      control={form.control}
                      name="badge_color"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>徽章颜色</FormLabel>
                          <FormControl>
                            <Input type="color" {...field} />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                  </div>

                  {/* 提交按钮 */}
                  <div className="flex justify-end gap-2">
                    <Button
                      type="button"
                      variant="outline"
                      onClick={() => navigate("/merchant/activity-requests")}
                      disabled={loading}
                    >
                      取消
                    </Button>
                    <Button type="submit" disabled={loading}>
                      {loading ? "提交中..." : "提交申请"}
                    </Button>
                  </div>
                </form>
              </Form>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  );
};

export default MerchantActivityRequestForm;
